.wheel-and-hamster {
  --dur: 1s;
  width: 12em;
  height: 12em;
  font-size: 8px;
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.wheel, .hamster, .hamster div, .spoke {
  position: absolute;
}

.wheel, .spoke {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  top: 0;
  left: 0;
}

.wheel {
  z-index: 2;
  background: radial-gradient(100% 100%, rgba(153, 153, 153, 0) 47.8%, #000 48%);
}

.hamster {
